import BoundingBoxMap from './BoundingBoxMap';
var BoundingBoxMapElement = /** @class */ (function () {
    function BoundingBoxMapElement() {
        var elements = document.querySelectorAll('.bboxmap');
        var _loop_1 = function (i) {
            var currentElementSection = elements[i];
            var selectElement = currentElementSection.querySelector('input[type="text"]');
            var newBoundingBoxStringList = selectElement.value;
            var map = new BoundingBoxMap(currentElementSection, newBoundingBoxStringList, function (newBoundingBoxStringList) {
                selectElement.value = newBoundingBoxStringList;
            });
            selectElement.addEventListener('change', function (event) {
                var inputTarget = event.target;
                inputTarget.classList.add('has-change');
                map.updateBoundingBox(inputTarget.value);
            });
        };
        for (var i = 0; i < elements.length; i++) {
            _loop_1(i);
        }
    }
    return BoundingBoxMapElement;
}());
new BoundingBoxMapElement();
